<template>
    <el-form ref="loginForm" :model="loginForm" :rules="rules" class="loginContainer" >
        <h3 class="loginTitle">系统登录</h3>
        <el-from-item prop="username">
            <el-input type="text" auto-comlete="false" v-model="loginForm.username" placeholder="请输入用户名"></el-input>
        </el-from-item>
        <el-from-item prop="password">
            <el-input type="password" auto-comlete="false" v-model="loginForm.password" placeholder="请输入密码"></el-input>
        </el-from-item>
        <el-from-item prop="code">
            <el-input type="text" auto-comlete="false" v-model="loginForm.code" placeholder="点击图片更换验证码" style="width: 250px; margin-right: 5px"></el-input>
            <img :src="captchaUrl" >
        </el-from-item>
        <el-checkbox v-model="checked" class="loginRemember">记住我</el-checkbox>
        <el-button type="primary" style="width: 100%" @click="submitLogin()">登录</el-button>
    </el-form> 
</template>

<script>

export default {
    name: "Login",
    data() {
        return {
            captchaUrl: '',
            loginForm: {
                username: "admin",
                password: "123",
                code: ""
            },
            checked: true,
            rules: {
                    username:[{required: true, message: "请输入用户名",trigger: 'blur'}],
                    password:[{required: true, message: "请输入密码", trigger: 'blur'}],
                    code: [{required: true, message: "请输入验证码", trigger: 'blur'}]
                }
            }
    },
    methods: {
        //登录点击事件
        submitLogin() {
            
            const form = ref(loginForm)
            form.validate((valid)=> {
                console.log(valid);
                if (valid) {
                    alert('submit!')
                } else {
                    this.$message.error('请输入所有字段信息！')
                    return false
                }
            })
        }
    }
}
</script>

<style>
    .loginContainer {
        display: flex;
        flex-direction: column; 
        border-radius: 15px;
        background-clip: padding-box;
        margin: 180px auto;
        width: 350px;
        padding: 15px 35px 15px 35px;
        background: #fff;
        border: 1px solid #eaeaea;
        box-shadow: 0 0 25px #cac6c6;
    }
    .loginTitle {
        margin: 0px auto 40px auto;
        text-align: center;
    }
    .loginRemember {
        text-align: left;
        margin: 15px 0px 15px 0px;
    }
</style>